.triangle {
  background: #D8D8D8;
  height: 38px;
  position: relative;
  width: 38px;
}

.triangle:after {
  content: "";
  position: absolute;
  right: 25%;
  top: 50%;
}

.triangle.right:after {
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 19px solid #9B9B9B;
  margin-top: -12px;
}

.triangle.left:after {
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-right: 19px solid #9B9B9B;
  margin-top: -12px;
}

